$(document).ready(function () {

  function moreLess(initiallyVisibleCharacters) {
    var visibleCharacters = initiallyVisibleCharacters;
    var paragraph = $(".text")


    paragraph.each(function () {
      var text = $(this).text();
      var wholeText = text.slice(0, visibleCharacters) + "<span class='ellipsis'>... </span><a href='#' class='more'>Ð§Ð¸Ñ‚Ð°Ñ‚ÑŒ Ð´Ð°Ð»ÑŒÑˆÐµ</a>" + "<span style='display:none'>" + text.slice(visibleCharacters, text.length) + "<a href='#' class='less'> Ð¡ÐºÑ€Ñ‹Ñ‚ÑŒ</span>"

      if (text.length < visibleCharacters) {
        return
      } else {
        $(this).html(wholeText)
      }
    });
    $(".more").on('click', function (e) {
      e.preventDefault();
      $(this).hide().prev().hide();
      $(this).next().show();
    });
    $(".less").on('click', function (e) {
      e.preventDefault();
      $(this).parent().hide().prev().show().prev().show();
    });
  };

  if (window.screen.width >= 100 && window.screen.width <= 768) {
    console.log(" Ð¼Ð°Ð»ÐµÐ½ÑŒÐºÐ¸Ð¹");

    moreLess(167);


  } else if (window.screen.width >= 768) {
    console.log(" ,Ð±Ð¾Ð»ÑŒÑˆÐ¾Ð¹");
    moreLess(310);


  }






});